@import url(https://fonts.googleapis.com/css2?family=Space+Mono:ital@1&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to left, rgb(137, 205, 216) 0%, rgba(14, 24, 34, 0.863) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h3,h2,p{
    font-family: 'Space Mono', monospace;
    color:rgb(0, 133, 156)
}

@font-face {
    font-family: 'SEGA LOGO FONT';
    font-style: normal;
    font-weight: normal;
    src: local('SEGA LOGO FONT'), url(/static/media/SEGA.47b4bfa9.woff) format('woff');
    }

h1{
    font-family: 'SEGA LOGO FONT';
    color:rgb(1, 32, 34)
}

